const ITEMNAME = 'Asset'

const _global = global || window

class Asset extends _global.jungle.MovingObject {
  /**
   * @access public
   * @param {assetConfig} [assetConfig] - The configuration object for the asset
   * @desc Creates a new Asset
   */
  constructor(assetConfig = {}, assetKit) {
    super(assetConfig)
    this.constructor = _global.jungle.MovingObject
    this.baseTypeName = ITEMNAME
    this._type = assetConfig.type || '_default'
    this.assetKit = assetKit
    this.animationQueue = []
    this.watched = false
  }

  isAsset() {
    return this.constructor === global.jungle.MovingObject && this.baseTypeName === 'Asset'
  }
}

module.exports = Asset
